<template>
  <div class="main-content">
    <div style="display: flex; align-items: flex-start; grid-gap: 10px">

      <div class="card" style="width: 150px">
        <div v-for="item in categoryList" :key="item.id"
             :class="{ 'category-item-active': item.name === current }" class="category-item" @click="selectCategory(item.name)">{{ item.name }}
        </div>
      </div>

      <div style="flex: 1;">
        <blog-list :categoryName="current" ref="blogListRef"></blog-list>
        <Footer/>


      </div>

      <div style="width: 260px">
        <div class="card" style="margin-bottom: 10px">
          <div style="font-size: 20px; font-weight: bold; margin-bottom: 10px ">欢迎您！😊</div>
          <a href="/front/person" v-if="this.user.username !== '666'">
            <div style="color: #666" v-if="this.user.username !== '666'">写下博客记录美好的一天</div>
          </a>
          <a  v-if="this.user.username === '666'">
            <div style="color: #ce0e0e;font-weight: bold;font-size: 16px">访客模式仅可查看部分内容哦</div>
          </a>
        </div>
        <div class="card" style="margin-bottom: 10px">
          <div style="display: flex; align-items: center; padding-bottom: 10px; border-bottom: 1px solid #ddd">
            <div style="font-size: 20px; flex: 1">文章榜单</div>
            <div style="font-size: 12px; color: #666; cursor: pointer;" @click="refreshTop"><i
                class="el-icon-refresh"></i> 换一换
            </div>
          </div>
          <div>
              <div v-for="item in showList" :key="item.id" class="line1" style="margin: 15px 0">
<!--                展示博客榜单-->
                <a :href="'/front/blogDetail?blogId=' + item.id" target="_blank">
                <span style="width: 18px; display: inline-block; text-align: right; margin-right: 10px">
                  <span v-if="item.index === 1" style="color: orangered">{{ item.index }}</span>
                  <span v-else-if="item.index === 2" style="color: goldenrod">{{ item.index }}</span>
                  <span v-else-if="item.index === 3" style="color: dodgerblue">{{ item.index }}</span>
                  <span v-else style="color: #666">{{ item.index }}</span>
                </span>
                <span style="color: #666;">{{ item.title }}</span>
                </a>
              </div>

          </div>
        </div>

        <div style="margin-bottom: 10px">
          <div v-for="item in topActivityList" :key="item.id" style="margin-bottom: 10px">
            <a :href="'/front/activityDetail?activityId=' + item.id" target="_blank"><img :src="item.cover" alt=""
                                                                                          style="width: 100%; border-radius: 5px"></a>
          </div>
        </div>

        <div style="line-height: 30px; color: #666; padding: 0 10px">
          <div>举报邮箱： xxxxxxxx@qq.com</div>
          <div> 座机电话： xxx-xxxxxxxx</div>
          <div> 网站备案模块©2024 西大迟意</div>
        </div>

      </div>

    </div>
  </div>
</template>

<script>

import Footer from "@/components/Footer";
import BlogList from "@/components/BlogList";

export default {
  components: {
    BlogList,
    Footer
  },
  data() {
    return {
      current: '全部博客',  //当前选中的分类名称
      categoryList: [],
      topList: [],
      showList: [],
      lastIndex: 0,
      topActivityList: [],
      user: JSON.parse(localStorage.getItem("xm-user") || '{}'),

    }
  },
  mounted() {
    this.load()
    this.refreshTop()

    this.loadTopActivity()

  },
  // methods：本页面所有的点击事件或者其他函数定义区
  methods: {
    loadTopActivity() {
      this.$request.get('/activity/selectTop').then(res => {
        console.log(res.data)
        this.topActivityList = res.data || []
      })
    },
    refreshTop() {
      this.$request.get('/blog/selectTop').then(res => {
        this.topList = res.data || []
        console.log(this.topList)
        let i = 1
        this.topList.forEach(item => item.index = i++)

        // 0  5  0
        if (this.lastIndex === 20) {
          this.lastIndex = 0
        }
        this.showList = this.topList.slice(this.lastIndex, this.lastIndex + 5)  // 0-5   5- 10  // 0-5
        this.lastIndex += 5  // 5  10  5
      })
    },
    selectCategory(categoryName) {
      this.current = categoryName
      // this.$refs.blogListRef.loadBlogs(1)  //永远慢半拍
    },
    load() {
      // 请求分类的数据
      this.$request.get('/category/selectAll').then(res => {
        this.categoryList = res.data || []
        this.categoryList.unshift({name: '全部博客'})
      })
    },


  }
}
</script>

<style>
.category-item {
  text-align: center;
  padding: 10px 0;
  font-size: 16px;
  cursor: pointer;
}

.category-item-active {
  background-color: #1890ff;
  color: #fff;
  border-radius: 5px;
}
</style>
